@import '../../variables+mixins+placeholders/_variables.sass';

.home {
    height: 100vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative; }
.bg {
    width: 80%;
    background-color: $yellow-color;
    margin-left: 10%;
    position: relative; }
.img {
    width: 100%;
    bottom: 0;
    right: 0;
    transform: translate(1.5rem,1.5rem); }
.title {
    margin: 0 10% 0 10%;
    font-size: $font-sizeBig;
    position: relative;
    overflow: hidden;
    &::before {
        content: "";
        width: 100%;
        height: 2px;
        background-color: $yellow-color;
        position: absolute;
        bottom: 0;
        animation: move-line 3s cubic-bezier(.82,-0.13,.64,1.15) both; } }
.subtitle {
    margin-left: 10%;
    font-size: $font-sizeSubtitle;
    margin-bottom: $margin-content; }


@keyframes move-line {
  0% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(100%); } }

@media (orientation: landscape) {
    .home {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center; }
    .titles {
        position: relative;
        width: 50%;
        display: flex;
        justify-content: center;
        flex-wrap: wrap; }
    .title {
        width: auto;
        text-align: center;
        margin: 0; }
    .subtitle {
        width: 100%;
        text-align: center;
        margin: 0; }
    .bg {
        margin: 0;
        height: 80%;
        width: 35%; }
    .img {
        width: auto;
        height: 100%; }

    @keyframes move-line {
        0% {
            transform: translateX(-200%); }
        100% {
            transform: translateX(100%); } } }
@media (orientation: landscape) and (min-width: 1024px) {
    .bg {
        width: 45%; }
    .img {
        position: absolute; } }
