@import '../../variables+mixins+placeholders/_variables.sass';

.container.active .title::before {
    transform: rotate(-90deg) translateX(-330%); }
.container.active .title::after {
    transform: translateX(100%); }
.project {
    margin-top: $margin-edge;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    padding: 10px;
    transition: .5s;
    transform: translateY(100%);
    opacity: 0;
    & img {
        width: 100%;
        box-shadow: 3px 3px 5px 0px rgba(0,0,0,0.75); }
    & p {
        text-align: justify;
        font-size: $font-sizeContent;
        margin-top: 20px; }
    &.active {
        transform: translateY(0);
        opacity: 1; } }

.button {
    height: 50px;
    width: 50px;
    background-color: $yellow-color;
    border: 1px solid black;
    color: black;
    text-align: center;
    font-size: $font-sizeBig;
    margin-top: 20px; }

@media (orientation: landscape) {
    .project {
        width: 40%; } }
