@import '../../variables+mixins+placeholders/_variables.sass';


.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $yellow-color;
    padding-bottom: $margin-edge; }
.sections {
    display: flex;
    flex-direction: column;
    align-items: center; }
@media (orientation: landscape) {
    .sections {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: flex-start; } }
